<template>
  <div class="login-wrapper p-r">
    <transition name="title" appear :duration="1500">
      <h2 class="mx-auto">
        <span class="iconfont icon-yinleyule"></span>
      </h2>
    </transition>

    <div class="content">
      <transition name="bg" appear :duration="1000">
        <div class="bg">
          <div class="title">
            <span>优我创享</span>
            <span>聆听你的美好</span>
          </div>
        </div>
      </transition>

      <div class="form-wrapper">
        <div class="type-tab p-r">
          <span
            @click="isLogin = true"
            :class="{
              active: isLogin,
            }"
            >密码登录</span
          >
          <span
            @click="isLogin = false"
            :class="{
              active: !isLogin,
            }"
            >账号注册</span
          >
        </div>
        <keep-alive :max="2" :include="['login', 'logon']">
          <login @change="isLogin = $event" v-if="isLogin" />
          <logon @change="isLogin = $event" v-else-if="!isLogin" />
        </keep-alive>
      </div>
    </div>

    <BgGraph
      class="p-a"
      style="right: 0; top: 0"
      type="fanCircle"
      :size="150"
    />

    <BgGraph
      class="p-a"
      style="right: 21vw; top: 10vw"
      type="circle"
      :size="99"
    />

    <BgGraph
      class="p-a"
      style="right: 0; bottom: 0px"
      position="leftTop"
      type="fanCircle"
      :size="288"
    />
    <BgGraph
      class="p-a"
      style="left: 0; bottom: 0px"
      position="rightTop"
      type="fanCircle"
      :size="88"
    />
    <BgGraph
      class="p-a"
      style="left: 5vh; top: 5vw"
      type="heart"
      :hasAnimate="false"
      :size="66"
    />
    <BgGraph
      class="p-a"
      style="left: 99px; top: 150px; transform: rotate(0deg)"
      type="leaf"
      :size="30"
    />
    <BgGraph
      class="p-a"
      style="left: 5vw; top: 18vw; transform: rotate(-27deg)"
      type="leaf"
      :size="27"
    />
    <BgGraph
      class="p-a"
      style="left:3vw; top: 27vw; transform: rotate(-15deg)"
      type="leaf"
      :size="88"
    />
  </div>
</template>

<script>
import login from "./login";
import logon from "./logon";
import BgGraph from "@/components/BgGraph";
export default {
  components: {
    login,
    logon,
    BgGraph,
  },
  data() {
    return {
      isLogin: true,
    };
  },
  created() {
    document.title = "登录注册";
  }
};
</script>

<style lang="less" scoped>
@import url("./header.less");
@import "~@/styles/myStyle.less";
.login-wrapper {
  width: 100%;
  padding-top: 30px;
  height: calc(100vh - 79px);
  box-sizing: border-box;
  .content {
    display: flex;
    width: 1140px;
    margin: 0 auto;
    height: calc(100vh - 79px - 58px);
    .bg {
      width: 800px;
      height: 100%;
      margin-right: -90px;
      background: url("~@/assets/svg/music.svg") no-repeat;
      background-position: center;
      background-size: contain;
      opacity: 0.8;
      display: flex;
      justify-content: center;
      align-items: center;
      .title {
        font-size: 50px;
        color: #fff;
        text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
        letter-spacing: 5px;
        font-family: PingFang-SC-Regular, Helvetica, "Microsoft Yahei";
        span {
          &:nth-child(1) {
            font-size: 60px;
            font-weight: 700;
          }
          display: block;
        }
      }
    }
  }
  .form-wrapper {
    position: relative;
    z-index: 1024;
    padding-top: 50px;
    width: 300px;
    margin: 0 auto;
    margin-top: 57px;
    align-self: flex-start;
    background: rgba(244, 243, 255, 0.1);
    border-radius: 12px;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
    opacity: 0.9;
    padding: 25px 30px;

    .type-tab {
      display: inline-block;
      font-size: 15px;
      color: #555;
      letter-spacing: 0;
      padding: 10px 0;
      margin-bottom: 10px;
      span {
        cursor: pointer;
        margin-right: 20px;
        &.active {
          color: #8982ff;
        }
      }
    }
  }
}
.bg-enter-active {
  -webkit-transition: 2s;
  transition: 2s;
}
.bg-enter {
  opacity: 0!important;
}

/deep/ .el-form-item.is-error .el-input__inner,
.el-form-item.is-error .el-input__inner:focus,
.el-form-item.is-error .el-textarea__inner,
.el-form-item.is-error .el-textarea__inner:focus,
.el-message-box__input input.invalid,
.el-message-box__input input.invalid:focus {
  border-color: #dcdfe6;
}
</style>