<template>
  <div
    class="heart-container"
    :class="{
      animate: hasAnimate,
    }"
    :style="{
      width: size + 'px',
      height: size + 'px',
      background: bgColor,
    }"
  ></div>
</template>

<script>
// 四分之一圆
export default {
  props: {
    size: {
      type: Number,
      default: 50,
    },
    bgColor: {
      type: String,
      default: "#F4F3FF",
    },
    hasAnimate: {
      type: Boolean,
      default: true,
    },
  },
};
</script>

<style scoped>
.heart-container {
  background-color: #8be9fd;
  transform: rotate(45deg);
  position: absolute;
}
.animate {
  animation: heart 3s ease-in infinite alternate-reverse;
}
.heart-container::before,
.heart-container::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: inherit;
  border-radius: 50%;
  left: -50%;
  top: 0;
}

div.heart-container::after {
  left: 0%;
  top: -50%;
}

@keyframes heart {
  0% {
    transform: rotate(45deg) scale(0.8);
  }

  100% {
    transform: rotate(45deg) scale(1);
  }
}
</style>