<template>
  <div
    :style="{
      width: size + 'px',
      height: size + 'px',
      background: bgColor,
      [positionMap[position]]: size + 'px',
    }"
  ></div>
</template>

<script>
// 四分之一圆
export default {
  props: {
    size: {
      type: Number,
      default: 50,
    },
    bgColor: {
      type: String,
      default: "#F4F3FF",
    },
    position : {
        type : String,
        default : "leftBottom"
    }
  },
  data() {
      return {
          positionMap : {
              "leftBottom" : 'border-bottom-left-radius',
              "rightBottom" : 'border-bottom-right-radius',
              "leftTop" : 'border-top-left-radius',
              "rightTop" : 'border-top-right-radius'
          }
      }
  }
};
</script>