<template>
  <component :hasAnimate="hasAnimate" :position="position" :size="size" :bgColor="bgColor" :is="graphMap[type]"></component>
</template>

<script>
// 背景图形管理
import Circle from "./Circle";
import FanCircle from "./FanCircle";
import Heart from "./Heart";
import Leaf from "./Leaf";
export default {
  props: {
    type: {
      type: String,
      default: "circle",
    },
    size: {
      type: Number,
      default: 50,
    },
    bgColor: String,
    position : String,
    hasAnimate : Boolean
    
  },
  data() {
    return {
      graphMap: {
        circle: Circle,
        fanCircle : FanCircle,
        heart : Heart,
        leaf : Leaf
      },
    };
  },
};
</script>

