<template>
  <el-form
    ref="formDom"
    :model="formDatas"
    label-width="0px"
    :rules="validator"
    label-position="left"
    :show-message="false"
    :hide-required-asterisk="true"
  >
    <el-form-item prop="user">
      <el-input placeholder="账号" v-model="formDatas.user"></el-input>
    </el-form-item>
    <el-form-item prop="pwd">
      <el-input
        placeholder="密码"
        type="password"
        v-model="formDatas.pwd"
      ></el-input>
    </el-form-item>
    <p style="margin-bottom: 18px; margin-top: -5px;padding-left:5px;">
      登录即代表你同意<a data-v-2c844fd9="" target="_blank" href="/404"
        >用户协议</a
      >和<a data-v-2c844fd9="" target="_blank" href="/404">隐私政策</a>
    </p>

    <div class="btn-wrapper">
      <el-button @click="handleLogin" :loading="isLoading" class="login-btn" >登录</el-button>
      <el-button @click="$emit('change',false)" class="logon-btn">注册</el-button>
    </div>
  </el-form>
</template>

<script>
import { mapState } from "vuex";

export default {
  props: {
    dialogTableVisible: {
      type: Boolean,
      require: true,
    },
  },
  data() {
    return {
      formDatas: {
        user: "",
        pwd: "",
      },
      validator: {
        //   表单校验
        user: [
          { required: true, message: "请填写账号名", trigger: "submit" },
          {
            min: 6,
            max: 15,
            message: "密码长度在 6 到 15 个字符",
            trigger: "submit",
          },
        ],
        pwd: [
          {
            validator: this.$utils.handleCheckPwd,
            trigger: "submit",
            required: true,
          },
        ],
      },
    };
  },
  created() {
    const user = localStorage.getItem("user");
    if (user) {
      this.$data.formDatas.user = user;
    }
  },
  computed: {
    ...mapState("User", ["isLoading"]),
  },
  methods: {
    async handleLogin() {
      this.$refs.formDom.validate(async (isTrue, obj) => {
        if (!isTrue) {
          // 表单验证不通过
          // 默认提示第一项信息
          const errFileds = Object.getOwnPropertyNames(obj);
          const errMsg = obj[errFileds[0]][0].message;
          this.$message({
            // 提示错误信息
            message: errMsg,
            type: "warning",
            offset: 80,
          });
        } else {
          const isLoginSuccess = await this.$store.dispatch("User/login", {
            user: this.$data.formDatas.user,
            pwd: this.$data.formDatas.pwd,
          });

          if (isLoginSuccess) {
            // 登录成功
            this.$message({
              // 提示登录成功信息
              message: "登录成功",
              type: "success",
              duration: 1500,
              offset: 80,
              onClose: () => {
                window.location.href= this.$route.query.returnUrl ? this.$route.query.returnUrl : "/" // 登录成功后跳转到首页
                // 后面首页会单独做一个页面
              },
            });
          } else {
            this.$message({
              // 提示错误信息
              message: "账号或密码不正确",
              type: "error",
              offset: 80,
            });
          }
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
/deep/.el-dialog__title {
  color: #409eff;
}
/deep/ .el-input__inner {
  background: transparent !important;
}
a {
  font-size: 0.85rem;
  margin-right: 1rem;
}
p {
  font-family: PingFangSC-Regular;
  font-size: 12px;
  color: #99a2aa;
  line-height: 16px;
  a {
    font-size: 12px;
    color: #00a1d6;
    margin-right: 0;
  }
}
.btn-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  .login-btn {
    width: 145px;
    border: 1px solid #8982ff;
    color: #fff;
    margin-right: 10px;
    background-color: #8982ff;
  }
  .logon-btn {
    width: 145px;
    border: 1px solid #8982ff;
    color: #1a1a1a;
    &:hover {
      background : lighten(#8982ff,20%);
    }
  }
}
</style>